import {noResults} from '../svgImgs';
import { t } from 'sn-translate';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import Loader from './Loader';
import '@servicenow/now-template-card';
import '@servicenow/now-button';

export default ({loading, resultList, totalPaginationCount, paginationOffset, properties: {searchQuery}}) => (
    <Fragment>
        {loading?<Loader/>:<div className="card-list">
            {resultList.length?
                <Fragment>{
                    resultList.map(({short_description, kb_knowledge_base, kb_category, display_number, number, workflow_state}, resultIndex) => (
                    <now-template-card-assist
                    append-to-payload={{resultIndex}}
                    manage-selected = {false}
                    content={[{value: {"type": "string", value: `${[kb_knowledge_base.displayValue, kb_category.displayValue].filter(Boolean).join(' | ')}`}}]}
                    heading={{ "label":short_description.displayValue }} 
                    tagline={{
                        icon: "document-fill",
                        label: `${display_number? display_number.displayValue:number.displayValue} · ${workflow_state.displayValue}`
                    }}/>
                    ))}
                    {(resultList.length < 20 && (totalPaginationCount - paginationOffset/20) > 1)?
                        <p className="pagination-warning">{20-resultList.length === 1?
                            t('1 item was removed from this list because of security constraints.'):
                            t('{0} items were removed from this list because of security constraints.', 20 - resultList.length)}
                        </p>: ''
                    }
                </Fragment>
                :
                [
                    noResults,
                    searchQuery?
                    <now-heading hasNoMargin={true} label={t("No results found")} level="4" purpose="header" variant="title-primary"/>:
                    <now-heading hasNoMargin={true} label={t("No articles in the knowledge base yet")} level="4" purpose="header" variant="title-primary"/>,
                    searchQuery?
                    <span className="no-results-help">{t('Try using different keywords.')}</span>:
                    <span className="no-results-help">{t('Any articles created within the knowledge base will show up here.')}</span>
                ]
                }
        </div>}
        {
            resultList.length?
            <div className="search-pagination">
                <now-button-iconic icon="arrow-left-fill" disabled={paginationOffset === 0} tooltipContent="Previous page" size="md" variant="tertiary" bare={true} configAria={{"aria-label":"Previous Page"}} append-to-payload={{previousResults: true}}/>
                <now-button-iconic icon="arrow-right-fill" disabled={(totalPaginationCount - paginationOffset/20) <= 1} tooltipContent="Next page" size="md" variant="tertiary" bare={true} configAria={{"aria-label":"Next Page"}} append-to-payload={{nextResults: true}}/>
            </div>:''
        }
    </Fragment>
);
