import updateArticleFromDocument from '../updateArticleFromDocument';
import {actionTypes} from '@servicenow/ui-core';
import {getShareableUrl} from '../Word';

const {COMPONENT_PROPERTY_CHANGED} = actionTypes

export default {
    [COMPONENT_PROPERTY_CHANGED]: async ({action: {payload: {name}}, dispatch, properties: {sysId, fields}}) => {
        if(name !== 'fields'){
            return;
        }
        const {office_doc_url} = fields;
        const unsavedForm = Object.values(fields).filter(field => field.name !== 'office_doc_url').some(field => field.originalValue !== field.value)
        dispatch('DIRTY_FORM_SET',unsavedForm);
        
        if(!sysId || (office_doc_url && office_doc_url.value) || office_doc_url.readonly)
            return;
        const url = await getShareableUrl();
        if(url){
            dispatch('SET_FIELDS_VALUE', {
                fields: {
                    office_doc_url: {
                        value: url,
                        displayValue: url
                    }
                }
            });
        }
    },
    ['NOW_BUTTON#CLICKED']: {
        modifier: {name: 'debounce', delay: 100},
        stopPropagation: true,
        effect: async ({properties:{fields, homePage, newArticle, sysId, officeInitialized}, action: {payload: {uiAction, actionSysId, name}, stopPropagation}, dispatch}) => {
            if(homePage){
                dispatch('ROUTE_CHANGED',{
                    page: 'form',
                    sysId: '-1'
                });
                return;
            }

            dispatch('CLEAR_NOTIFICATIONS', {
                notificationAction: {
                    type: 'clear'
                }
            });

            const isInvalid = Object.values(fields).some(field => (field.mandatory && !field.value) || field.isInvalid);
            if(!isInvalid)
                dispatch('LOADING_STARTED');
            if(newArticle){
                dispatch('GFORM#SAVE');
                if(!isInvalid){
                    dispatch('DIRTY_FORM_SET',false);
                    dispatch('CUSTOM_PROPERTY_SET', fields.number.value);
                }
            } else if(uiAction){
                if(!isInvalid && officeInitialized && ['publish_knowledge','sysverb_ws_save'].includes(name)){
                    await updateArticleFromDocument(sysId).then(() => dispatch('CUSTOM_PROPERTY_SET', fields.number.value));
                }
                dispatch('NOW_RECORD_UI_ACTION_BAR#CLICKED',{actionSysId});
                dispatch('UI_ACTIONS_LOAD_INITIATED', {sysId});
            }
        }
    }
};
