import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {setCustomProperty, setAutoOpenAddin, isContentEmpty, insertContentControl} from '../Word';
import updateArticleFromDocument from '../updateArticleFromDocument';

const articleQuery = `
query($query: String!) {
    GlideRecord_Query{
        kb_knowledge(queryConditions: $query){
            _results{
                workflow_state{
                    displayValue,
                    value
                },
                sys_id{
                    value
                },
                number{
                    value
                },
                text{
                    displayValue
                },
                office_doc_url{
                    value
                }
            }
        }
    }
}
`;

const uiActionQuery = `
    query($sysId: String!) {
        GlideUIAction_Query{
            uiActions(table:"kb_knowledge", sysId:$sysId, view:"word_addin"){
                formActions{
                    label,
                    sysId,
                    name
                }
            }
        }
    }
`;

const resetDocument = async (dispatch, updateState) => {
    await setCustomProperty('sn_kb_number', '');
    dispatch('ROUTE_CHANGED',{
        page: 'home'
    });
    setTimeout(() => updateState({
        customPropertySet: false,
        historyCount: 0
    }),100);
};

export const formActions = {
    ['ARTICLE_SELECTED']: ({properties: {sysId, number}, dispatch, updateState}) => {
        if(sysId === '-1'){
            updateState({loading:false});
            return;
        }
        let query;
        if(sysId){
            query = `sys_id=${sysId}`;
            dispatch('UI_ACTIONS_LOAD_INITIATED',{sysId});
        }
        else if(number)
            query = `number=${number}^workflow_state=draft^ORworkflow_state=published^ORworkflow_state=review^ORworkflow_state=scheduled_publish`;
        dispatch('ARTICLE_FORM_LOAD_STARTED',{query});
    },
    ['ARTICLE_FORM_LOAD_STARTED']: createGraphQLEffect(articleQuery,{
        variableList: ['query'],
        successActionType: 'ARTICLE_DATA_LOADED',
        errorActionType: 'ADDIN#REQUEST_FAILED'
    }),
    ['ARTICLE_DATA_LOADED']: async ({action, properties: {sysId}, state: {customPropertySet, officeInitialized, loadedArticle}, updateState, updateProperties, dispatch}) => {
        const results = action.payload.data.GlideRecord_Query.kb_knowledge._results;
        
        let result = results[0];
        if(results.length > 1){
            const stateMap = results.reduce((map, article, index) => (map[article.workflow_state.value] = {index}, map),{});
            const {index} = stateMap['review'] || stateMap['scheduled_publish'] || stateMap['draft'] || stateMap['published'];
            result = results[index];
        }

        if(customPropertySet && !result.office_doc_url.value)
            return await resetDocument(dispatch, updateState);
        
        if(sysId !== result.sys_id.value)
            dispatch('UI_ACTIONS_LOAD_INITIATED',{sysId: result.sys_id.value});
        
        const currentArticleNumber = result.number.value;

        updateProperties({
            number: currentArticleNumber,
            sysId: result.sys_id.value
        });
        updateState({
            loading: false
        });

        if(currentArticleNumber!==loadedArticle &&officeInitialized && !customPropertySet){
            const isEmpty = await isContentEmpty();
            const articleContent = result.text.displayValue;
            if(isEmpty && articleContent){
                await insertContentControl('Article body', 'article', 'Hidden', true, articleContent, 'Start')
                updateState({loadedArticle: currentArticleNumber});
            }
            else {
                updateState({
                    showWarning: true,
                    articleContent
                });
            }
        }
    },
    ['CLOSE_CONTENT_CHANGED_WARNING']: ({updateState, properties: {number}}) => updateState({
        showWarning: false,
        articleContent: undefined,
        loadedArticle: number
    }),
    ['REPLACE_CONTENT']: ({state: {articleContent, officeInitialized, customPropertySet}, properties:{number}, updateState}) => {
        if(officeInitialized && !customPropertySet){
            insertContentControl('Article body', 'article', 'Hidden', true, articleContent, 'Start')
            updateState({
                articleContent: undefined,
                showWarning: false,
                loadedArticle: number
            });
        }
    },
    ['UI_ACTIONS_LOAD_INITIATED']: createGraphQLEffect(uiActionQuery, {
        variableList: ['sysId'],
        successActionType: 'UI_ACTIONS_LOADED',
        errorActionType: 'ADDIN#REQUEST_FAILED'
    }),
    ['UI_ACTIONS_LOADED']: ({action, updateState}) => updateState({
        loading: false,
        uiActions: action.payload.data.GlideUIAction_Query.uiActions.formActions.filter(action => ['publish_knowledge','checkout_knowledge','sysverb_ws_save','recall'].includes(action.name))
    }),
    ['PREVIEW_RECORD']: ({action: {payload: {table, sys_id: sysId}}, dispatch}) => {
        if(table === 'kb_knowledge'){
            dispatch('ROUTE_CHANGED',{sysId})
        } else {
            open(`/${table}.do?sys_id=${sysId}`);
        }
    },
    ['RECORD_STATUS_CHANGED']: ({action: {payload: {sys_id, status}}, properties: {sysId}, state:{officeInitialized}, dispatch, updateState}) => {
        if(status === 'inserted' && sysId === '-1' && sys_id !== '-1'){
            if(officeInitialized){
                updateState({loading: true})
                updateArticleFromDocument(sys_id).then(() => {
                    dispatch('ROUTE_CHANGED',{sysId: sys_id});
                });
            } else {
                dispatch('ROUTE_CHANGED',{sysId: sys_id});
            }
        }
    },
    ['CUSTOM_PROPERTY_SET']: ({action, state: {officeInitialized}, updateState}) => {
        if(officeInitialized){
            setCustomProperty('sn_kb_number', action.payload)
                .then(() => setAutoOpenAddin(true))
                .then(() => updateState({customPropertySet: true}));
        }
    },
    ['LOADING_STARTED']: ({updateState}) => updateState({loading: true})
};
