import '@servicenow/now-card';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '../now-kb-office-addin-footer';
import {noResults} from '../svgImgs';
import { t } from 'sn-translate';

export default ({kbList, canContribute}) => (
    <Fragment>
        <div className="card-list">
            {kbList.length?
                kbList.map(kb => (
                    <now-card key={kb.sys_id.value} size="lg" interaction="click" append-to-payload={{id: kb.sys_id.value, title: kb.title.displayValue}}>
                        <now-card-header heading={{label: kb.title.displayValue, lines:2}} caption={{label:kb.description.displayValue}}/>
                    </now-card>
                )):
                [
                    noResults,
                    <now-heading hasNoMargin={true} label={t("No knowledge bases yet")} level="4" purpose="header" variant="title-primary"></now-heading>
                ]
            }
        </div>
        {canContribute?
            <now-kb-office-addin-footer home-page={true}/>:''
        }
    </Fragment>
);
