import {getAllBlobs, preProcessHtml} from './helpers';

export const initializeOffice = () => new Promise((resolve, reject) => {
    try{
        Office.initialize = resolve;
    }catch(e){
        reject();
    }
});

export const getDocumentUrl = () => new Promise((resolve, reject) => 
    Office.context.document.getFilePropertiesAsync(({value, status}) => 
        (status === 'succeeded')? resolve(value.url): reject())
);

export const getShareableUrl = () => new Promise(async resolve => {
    const urlComponent = '_layouts/WordViewer.aspx';
    const url = new URL(await getDocumentUrl());
    const path = url.pathname;
    const isOneDrive = url.host.endsWith('-my.sharepoint.com');

    if(isOneDrive || url.host.endsWith('.sharepoint.com')){
        const pathMatcher = path.match(isOneDrive? '\/personal\/(.*?)\/': '\/sites\/(.*?)\/');
        if(pathMatcher){
            url.pathname = pathMatcher[0] + urlComponent;
            url.searchParams.set('id', decodeURIComponent(path.replace(pathMatcher[0], '')));
            resolve(url.href);
            return;
        }
    }
    resolve(null);
});

export const getCustomProperty = prop => new Promise(resolve => {
    Word.run(async context => {
        const property = context.document.properties.customProperties.getItemOrNullObject(prop);
        context.load(property);
        await context.sync();
        resolve(property.value);
    });
});

export const setCustomProperty = (prop, value) => new Promise(resolve => {
    Word.run(async context => {
        context.document.properties.customProperties.add(prop,value);
        await context.sync();
        resolve();
    });
});

export const insertContentControl = (title, tag, appearance = 'BoundingBox', clearContent = true, html = '', location = 'Start') => new Promise(resolve => {
    Word.run(async context => {
        const body = context.document.body;
        if(clearContent) body.clear();

        const control = body.insertContentControl();
        control.tag = tag;
        control.title = title;
        control.appearance = appearance;

        if(html) control.insertHtml(await preProcessHtml(html), location);
        
        const range = context.document.getSelection();
        range.select(location);
        await context.sync();
        resolve();
    });
});

export const isContentEmpty = () => new Promise(resolve => {
    Word.run(async context => {
        const html = context.document.body.getHtml();
        await context.sync();
        const text = new DOMParser().parseFromString(html.value, 'text/html').body.textContent;
        resolve(!text.trim());
    });
});

export const setAutoOpenAddin = shouldOpen => new Promise((resolve, reject) => {
    Office.context.document.settings.set("Office.AutoShowTaskpaneWithDocument", shouldOpen);
    Office.context.document.settings.saveAsync(result => {
        if(result.status === Office.AsyncResultStatus.Succeeded){
            resolve();
        } else {
            reject(result.error);
        }
    });
});

export const getDocument = fileName => new Promise((resolve, reject) => {
    Office.context.document.getFileAsync(Office.FileType.Compressed, { sliceSize: 750*1024 }, async result => {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
            const blobs = await getAllBlobs(result.value);
            const docxFile = new File(blobs, fileName + '.docx', {
                type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'
            });
            resolve({
                file: docxFile,
                success: true
            });
            return;
        }
        reject({error: true});
    });
});
