import {actionTypes} from '@servicenow/ui-core';
import {createAmbEventEffect} from '@servicenow/ui-effect-amb';
import {homeActions} from './homeActions';
import {searchActions} from './searchActions';
import {formActions} from './formActions';
import {getCustomProperty, initializeOffice} from '../Word';
import {getComponentsByTagNames} from '@devsnc/library-uxf';
const {COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED} = actionTypes;

const paramPropertyMap = {
    page: 'page',
    number: 'number',
    sysId: 'id',
    searchQuery: 'query',
    selectedKb: 'kb'
};

const getPath = ({page, sysId, number, searchQuery, selectedKb}) => {
    let path = `${location.pathname}?page=${page}`
    switch(page){
        case 'search': {
            if(searchQuery)
                path = `${path}&query=${searchQuery}`;
            if(selectedKb)
                path = `${path}&kb=${selectedKb}`;
            return path;
        };
        case 'form': {
            if(sysId)
                path = `${path}&id=${sysId || '-1'}`
            else
                path = `${path}&number=${number}`;
            return path;
        };
        default: return path;
    }
};

const execRouterActions = (page, dispatch, updateState) => {
    if(page !== 'form')
        updateState({
            loadedArticle: '',
            loading: true
        });
    else
        updateState({loading: true});

    switch(page){
        case 'search': dispatch('SEARCH_PERFORMED'); break;
        case 'form': dispatch('ARTICLE_SELECTED'); break;
        default: dispatch('KB_LIST_REQUESTED'); break;
    }
};

const handleLogout = ({updateProperties}) => updateProperties({loggedIn: false});

export const actionHandlers = {
    [COMPONENT_BOOTSTRAPPED]: ({properties, dispatch, updateState, updateProperties}) => {
        if(window !== top){
            focus();
            onfocus = () => dispatch('ADDIN_FOCUSED');
        }
        getComponentsByTagNames(['sn-kb-category-picker']);
        dispatch('AMB_EVENT_SUBSCRIPTION_CHANGED', {subscribe: true});

        const setupRouter = props => {
            updateProperties(props);
            execRouterActions(props.page, dispatch, updateState);
            history.replaceState(props, props.page, getPath(props));
            onpopstate = e => dispatch('HISTORY_BACK_PRESSED', {props: e.state});
        };

        const params = new URLSearchParams(location.search);
        const newProps = {page: 'home'};
        ['page','sysId','number','searchQuery','selectedKb'].forEach(prop => {
            newProps[prop] = params.get(paramPropertyMap[prop]) || properties[prop] || newProps[prop];
        });
        if(properties.addIn){
            initializeOffice().then(async () => {
                updateState({officeInitialized: true})
                const kbNumber = await getCustomProperty('sn_kb_number');
                if(kbNumber){
                    updateState({customPropertySet: true});
                    setupRouter({
                        page: 'form',
                        sysId: '',
                        number: kbNumber
                    });
                } else {
                    setupRouter(newProps);
                }
            });
        } else {
            setupRouter(newProps);    
        }
    },
    [COMPONENT_DISCONNECTED]: () => {
        dispatch('AMB_EVENT_SUBSCRIPTION_CHANGED', {subscribe: false});
    },
    ['AMB_EVENT_SUBSCRIPTION_CHANGED']: createAmbEventEffect(),
    ['AMB_EVENT_SESSION_LOGGED_OUT']: handleLogout,
    ['AMB_EVENT_SESSION_INVALIDATED']: handleLogout,
    ['NOW_BUTTON_BARE#CLICKED']: ({action:{payload:{loginPage}}}) => loginPage? open(`${location.origin}/sn_km_word_kb_login_helper.do`): null,
    ['NOW_BUTTON_ICONIC#CLICKED']: ({action: {payload: {nextResults, previousResults}}, state: {paginationOffset}, dispatch, updateState}) => {
        if(nextResults || previousResults){
            updateState({
                paginationOffset: nextResults? paginationOffset + 20: paginationOffset - 20,
                loading: true
            });
            dispatch('SEARCH_PERFORMED');
        }
    },
    ['ADDIN_FOCUSED']: ({properties:{loggedIn}}) => !loggedIn?location.reload():null,
    ['ADDIN#REQUEST_FAILED']: ({action, updateProperties}) => {
        const status = action.payload.response.status;
        if(status >= 400 && status < 500)
            updateProperties({loggedIn: false});
    },
    ['NOW_CARD#CLICKED']: ({action, dispatch, updateState}) => {
        const {id, title} = action.payload;
        dispatch('ROUTE_CHANGED',{
            page: 'search',
            selectedKb: id
        });
        updateState({
            selectedKbTitle: title
        });
    },
    ['NOW_PILL#DISMISSED']: ({action: {payload: {searchPill}}, properties: {searchQuery}, dispatch}) => {
        if(searchPill){
            if(searchQuery){
                dispatch('ROUTE_CHANGED', {
                    page: 'search',
                    selectedKb: null,
                    searchQuery
                });
            } else {
                dispatch('ROUTE_CHANGED', {
                    page: 'home',
                    selectedKb: null
                });
            }
        }
    },
    ['NOW_TEMPLATE_CARD_ASSIST#SELECTED_SET']: ({state, action, dispatch}) => {
        const {resultIndex} = action.payload;
        const {resultList} = state;
        const article = resultList[resultIndex];
        if(article.office_doc_url && article.office_doc_url.value){
            open(article.office_doc_url.value);
        } else if(article.sys_class_name.value !== 'kb_knowledge'){
            open(`/${article.sys_class_name.value}.do?sys_id=${article.sys_id.value}`)
        } else {
            dispatch('ROUTE_CHANGED',{
                page: 'form',
                sysId: article.sys_id.value
            });
        }
    },
    ['NOW_MODAL#OPENED_SET']: ({action: {payload: {contentChangedWarning, dirtyFormWarning}}, dispatch}) => { 
        if(dirtyFormWarning)
            dispatch('REDIRECT_CANCELLED');
        if(contentChangedWarning)
            dispatch('CLOSE_CONTENT_CHANGED_WARNING');
    },
    ['ROUTE_CHANGED']: {
        modifier: {name: 'debounce', delay: 50},
        effect: ({properties: {page: oldPage, searchQuery: query, selectedKb: kb}, state:{historyCount, dirtyForm}, updateState, updateProperties, dispatch, action:{
                payload: {
                    page = oldPage,
                    searchQuery = page==='search'? query: null,
                    selectedKb = page==='search'? kb: null,
                    sysId = null,
                    number = null,
                }
            }}) => {
            const props = {page, searchQuery, selectedKb, sysId, number};
            if(dirtyForm){
                updateState({dirtyFormRedirect: props})
                return;
            }
            const nextPath = getPath(props);
            history.pushState(props, page, nextPath);
            updateProperties(props);
            updateState({historyCount: historyCount + 1});
            execRouterActions(page, dispatch, updateState);
        }
    },
    ['HISTORY_BACK_PRESSED']: ({action: {payload: {props}}, state:{historyCount, customPropertySet}, updateProperties, updateState, dispatch}) => {
        if(!customPropertySet){
            updateProperties(props);
            if(historyCount > 0)
                updateState({historyCount: historyCount - 1});
            execRouterActions(props.page, dispatch, updateState);
        }
    },
    ['BACK_PRESSED']: ({state:{dirtyForm}, updateState}) => {
        if(dirtyForm){
            updateState({
                dirtyFormRedirect: true,
                goBackOnRedirect: true
            });
            return;
        }
        history.back();
    },
    ['DIRTY_FORM_SET']: ({action: {payload: dirtyForm}, updateState}) => {
        updateState({dirtyForm});
        onbeforeunload = dirtyForm? () => true: null;
    },
    ['REDIRECT_CONTINUED']: ({state:{dirtyFormRedirect, goBackOnRedirect}, dispatch, updateState}) => {
        updateState({
            dirtyFormRedirect: undefined,
            goBackOnRedirect: undefined
        });
        dispatch('DIRTY_FORM_SET', false);
        if(goBackOnRedirect)
            history.back();
        else
            dispatch('ROUTE_CHANGED', dirtyFormRedirect);
    },
    ['REDIRECT_CANCELLED']: ({updateState}) => updateState({dirtyFormRedirect: undefined, goBackOnRedirect: undefined}),
    ...homeActions,
    ...searchActions,
    ...formActions
}
