import {getDocument} from './Word';

export default async sysId => {
    const {success, error, file} = await getDocument('kb_addin_doc');
    if(error && !success){
        return {error};
    }

    return await fetch(`/api/sn_km_word/knowledge_addin/kb_document_update?sys_id=${sysId}`, {
        method: "POST",
        body: file,
        headers: {
            'x-usertoken': g_ck
        }
    }).then(response => response.json());
};
