import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';

export default ({properties: {newArticle,homePage,actions}}) => {
    if(homePage || newArticle)
        return <now-button label={newArticle? t('Submit'): t('Create article')} size="md" variant="secondary" append-to-payload={{name:newArticle}}/>;
    if(actions.length > 0){
        return (
            <Fragment>
                {actions.map(({sysId, label, name}) => (
                    <now-button label={label} size="md" variant="secondary" append-to-payload={{
                        uiAction: true,
                        actionSysId:sysId,
                        name
                    }}/>
                ))}
            </Fragment>
        );
    }
};
