import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';
import Router from './routes';
import {sessionExpired} from './svgImgs';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import '@servicenow/now-modal';
import '@servicenow/now-tooltip';
import '@servicenow/now-pill';

const iconWithToolTip = (icon, toolTip, id) => (
    <Fragment>
        {icon}
        <now-tooltip id={id} target-ref={icon.elm} content={toolTip} delay={200}/>
    </Fragment>
);

const backIcon = <now-icon icon="chevron-left-fill" aria-describedby="backToolTip" size="lg"/>;
const homeIcon = <now-icon icon="home-outline" aria-describedby="homeToolTip" size="lg"/>;
const infoIcon = <now-icon icon="circle-info-outline" aria-describedby="infoToolTip" size="lg"/>;
const reloadIcon = <now-icon icon="arrow-counterclockwise-outline" aria-describedby="reloadToolTip" size="lg"/>;

const Navbar = ({page, dispatch, historyCount, displayNavButtons, loggedIn}) => (
    <nav className="kb-office-nav">
        {(displayNavButtons && historyCount > 0)?
            <button on-click={() => dispatch('BACK_PRESSED')}>{iconWithToolTip(backIcon, t('Go back to the previous view'), 'backToolTip')}</button>:<div/>
        }
        <img src="/styles/images/now-logo.svg" on-click={() => open(location.origin)}/>
        {(displayNavButtons && page!=='home')?
            <button on-click={() => dispatch('ROUTE_CHANGED',{page: 'home'})}>{iconWithToolTip(homeIcon, t('Go to the landing screen'), 'homeToolTip')}</button>:
        !loggedIn?
            <button on-click={() => location.reload()}>{iconWithToolTip(reloadIcon, t('Reload the add-in'), 'reloadToolTip')}</button>:
        loggedIn && !displayNavButtons?
            <div>{iconWithToolTip(infoIcon, t('The article is linked to the current document. To create or access other articles, open another document in Word.'), 'infoToolTip')}</div>:''}
    </nav>
);

const loginPage = <Fragment>
    <Navbar displayNavButtons={false} loggedIn={false}/>
    <main>
        {sessionExpired}
        <now-heading className="login-title" label={t("Session Inactive")} has-no-margin={true} level="1" variant="header-secondary"/>
        <p className="login-msg">{t('Log in to your ServiceNow account.')}</p>
        <ul className="login-actions">
            <li>
                <now-icon icon="circle-arrow-down-outline"/>
                <now-button-bare variant="secondary" label={t("Log in to ServiceNow")} iconEnd="open-link-outline" append-to-payload={{loginPage:true}}/>
            </li>
            <li>{t('Opens the ServiceNow login page in a new tab.')}</li>
        </ul>
    </main>
</Fragment>;

const WarningModal = ({showWarning}) => <now-modal 
    header-label={t('What do you want to do?')}
    footer-actions={[{
        label:t("Retain"),
        variant: "primary",
        clickActionType: 'CLOSE_CONTENT_CHANGED_WARNING'
    },{
        label:t("Replace"),
        variant: "secondary",
        clickActionType: 'REPLACE_CONTENT'
    }]}
    size="sm"
    opened={showWarning}
    manage-opened
    append-to-payload={{contentChangedWarning: true}}>
        <ul style={{cssText: 'padding-left:20px'}}>
            <li>{t('Select Replace to overwrite the existing content in the Word document with content from the article you selected.')}</li>
            <li>{t('Select Retain to keep the existing content in the Word document.')}</li>
        </ul>
        {t('Note: When you save the article, the Word document content will replace the article content.')}
    </now-modal>;

const DirtyFormModal = ({showWarning}) => <now-modal 
    header-label={t('Confirm navigation')}
    content={t('You have unsaved changes on this form. If you continue, your changes will be lost.')}
    footer-actions={[{
        label:t("Continue"),
        variant: "primary",
        clickActionType: 'REDIRECT_CONTINUED'
    },{
        label:t("Cancel"),
        variant: "secondary",
        clickActionType: 'REDIRECT_CANCELLED'
    }]}
    size="sm"
    opened={showWarning}
    manage-opened
    append-to-payload={{dirtyFormWarning: true}}/>;

export default (state, {dispatch, updateState}) => {
    const {properties:{page, searchQuery, loggedIn, selectedKb}, historyCount, customPropertySet, showWarning, dirtyFormRedirect, selectedKbTitle} = state;
    const charLimit = 3;
    if(!loggedIn){
        return loginPage;
    };
    return (
        <Fragment>
            <Navbar page={page} historyCount={historyCount} loggedIn={loggedIn} dispatch={dispatch} displayNavButtons={!customPropertySet}/>
            <main>
                <WarningModal showWarning={showWarning}/>
                <DirtyFormModal showWarning={dirtyFormRedirect? true: false}/>
                {(page && page !== 'form')?
                    (<Fragment>
                        <span className="page-header">
                            <now-heading has-no-margin label={page==='search'? t('Search'): t('Welcome to Knowledge')} level="2" variant="header-secondary"/>
                            {
                                page==='search' && selectedKb?
                                <now-pill label={selectedKbTitle} size="md" canDismiss={true} append-to-payload={{searchPill:true}}/>:''
                            }
                        </span>
                        <input type="text" autoComplete="off" value={searchQuery} className="search-input"
                        on-change={e => charLimit && (e.target.value.length >= charLimit)? dispatch('ROUTE_CHANGED', {page:'search',searchQuery: e.target.value}): ''}
                        placeholder={t('Search (minimum {0} characters)', charLimit)}/>
                    </Fragment>):''
                }
                <Router {...state} dispatch={dispatch} updateState={updateState}/>
            </main>
        </Fragment>
    )
};
