import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

const searchGqlQuery = `
query($queryConditions: String!, $paginationOffset: Int) {
	GlideRecord_Query{
        kb_knowledge(queryConditions: $queryConditions, pagination: {limit: 20, offset: $paginationOffset}){
            _results{
                sys_class_name{
                    value
                },
                number{
                    displayValue
                },
                display_number {
                    displayValue
                },
                workflow_state{
                    displayValue
                },
                short_description{
                    displayValue
                },
                kb_knowledge_base{
                    displayValue
                },
                sys_id{
                    value
                },
                kb_category{
                    displayValue
                },
                office_doc_url{
                  value
                }
            }
            _rowCount
        }
    }
}
`;

export const searchActions = {
    ['SEARCH_PERFORMED']: ({properties, state: {paginationOffset}, dispatch}) => {
        const {selectedKb, searchQuery} = properties;
        const commonQuery = 'active=true^sys_class_name!=kb_knowledge_block^workflow_state=draft^ORworkflow_state=published^ORworkflow_state=review^ORworkflow_state=scheduled_publish^kb_knowledge_base.active=true';
        let queryConditions = searchQuery?`GOTO123TEXTQUERY321=${searchQuery}^${commonQuery}`:commonQuery;
        if(selectedKb){
            queryConditions += `^kb_knowledge_base=${selectedKb}`;
        }
        dispatch('SEARCH_REQUESTED',{queryConditions, paginationOffset})
    },
    ['SEARCH_REQUESTED']: createGraphQLEffect(searchGqlQuery,{
        variableList: ['queryConditions', 'paginationOffset'],
        successActionType: 'SEARCH_RESULTS_LOADED',
        errorActionType: 'ADDIN#REQUEST_FAILED'
    }),
    ['SEARCH_RESULTS_LOADED']: ({action, updateState}) => updateState({
        resultList: action.payload.data.GlideRecord_Query.kb_knowledge._results,
        totalPaginationCount: Math.ceil(action.payload.data.GlideRecord_Query.kb_knowledge._rowCount/20),
        loading: false
    })
}
