import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';

const query = `
query {
    GlideRecord_Query{
        kb_knowledge_base(queryConditions:"active=true"){
            _results{
                description{
                    displayValue
                }
                sys_id{
                    value
                }
                title{
                    displayValue
                }
            }
        }
    }
    AppKnowledge_Query{
        getContributableKnowledgeBases{
            value
            displayValue
        }
    }
}
`;

export const homeActions = {
    ['KB_LIST_REQUESTED']: createGraphQLEffect(query, {
        successActionType: 'KB_LIST_LOADED',
        errorActionType: 'ADDIN#REQUEST_FAILED'
    }),
    ['KB_LIST_LOADED']: ({action,updateState}) => {
        const kbList = action.payload.data.GlideRecord_Query.kb_knowledge_base._results;
        const knowledgeBases = action.payload.data.AppKnowledge_Query.getContributableKnowledgeBases;
        updateState({
            kbList,
            canContribute: knowledgeBases.length > 0,
            loading: false
        });
    }
}
