const promiseAll = promises => {
    if(Promise.allSettled){
        return Promise.allSettled(promises).then(reqs => reqs.map(({status,value}) => status==='fulfilled'?value: null));
    } else {
        return Promise.all(promises.map(p => p.catch(() => null)));
    }
};

const isNowURL = img => {
    try{
        const url = new URL(img.src);
        return url.origin === new URL(document.baseURI).origin;
    }
    catch(e){
        return true;
    }
};

const fetchImages = imgs => {
    const imgRequests = imgs.map(img => {
        if(isNowURL(img)){
            return fetch(img.src).then(response => 
                new Promise(async (resolve, reject) => {
                    const reader = new FileReader()
                    reader.onloadend = () => resolve(reader.result)
                    reader.onerror = reject;
                    reader.readAsDataURL(await response.blob());
                })
            )
        } else {
            return Promise.resolve(img.src);
        }
    });
    return imgRequests.length > 0? promiseAll(imgRequests): Promise.resolve([]);
};
export const getAllBlobs = async file => {
    const blobs = [];
    let count = file.sliceCount;
    while (count --> 0) {
        const sliceIndex = file.sliceCount - count - 1;
        blobs.push(await new Promise((resolve, reject) => 
            file.getSliceAsync(sliceIndex, ({status, value: {data}}) => 
                status === Office.AsyncResultStatus.Succeeded? resolve(new Blob([new Uint8Array(data)])): file.closeAsync(reject)
            )
        ));
    }
    file.closeAsync();
    return blobs;
};

export const preProcessHtml = async content => {
    const doc = new DOMParser().parseFromString(content, 'text/html');
    var base = doc.createElement('base');
    base.href = location.origin;
    doc.head.appendChild(base);
    const images = Array.from(doc.images);
    const imgData = await fetchImages(images);
    imgData.forEach((data, index) => data? images[index].src = data: null);

    return doc.body.innerHTML;
};
